.card{
    background-color: #ccc;
    padding: 0px;
    border: 2px solid black;
    border-radius: 15px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}
.card:hover{
    transform: scale(1.05);
}